* {
  background: rgb(42, 43, 44);
}
.Miaw {
  margin: auto;
  padding-top: 10%;
  text-align: center;
  width: 100%;
  height: 100%;
}

button {
  margin-top: 20px;
  color: rgb(255, 255, 255);
  border: 1px solid white;
  border-radius: 10px;
  cursor: pointer;
}

button:hover {
  color: burlywood;
  
}
img {
  max-height:30% ;
  max-width: 30%;
  border-radius: 2%;
  box-shadow: 0 3px 20px rgb(140, 132, 132);
}

footer {
  color: gray;
  margin-top: 200px;
}

a {
  color: rgb(208, 208, 208);
}

@media only screen and (max-width: 500px) {
  img {
    margin-top: 40%;
    max-height:90% ;
    max-width: 90%; 
  }
}